import React, {Component, Fragment} from 'react';
import { Responsive, Segment, Button, Icon, Menu, Grid, Container,Embed, Divider, Image } from 'semantic-ui-react';
import './App.css';
import DiagonalSpacer from "./components/DiagonalSpacer";
